import React from 'react';
import Types from 'prop-types';

import classNames from 'classnames';

export const AlertType = {
  Info: 'info',
  Warning: 'warning',
  Error: 'error',
  Success: 'success',
};

const Alert = ({ children, type, dismissible, small, pointer, focusVisible }) => {
  const alertClass = {
    'alert-success': type === 'success',
    'alert-info': type === 'info',
    'alert-warning': type === 'warning',
    'alert-danger': type === 'error',
    'alert-focus': focusVisible,
    arrow: pointer,
    small,
    'p-x-2': small,
    'p-y-1': small,
  };

  return (
    <div role="alert" className={classNames('alert', 'alert-detach', alertClass)}>
      {dismissible && (
        <button type="button" className="close" data-dismiss="alert" aria-label="Close">
          <span aria-hidden="true">×</span>
        </button>
      )}
      {children}
    </div>
  );
};

Alert.propTypes = {
  type: Types.string,
  children: Types.node.isRequired,
  small: Types.bool,
  dismissible: Types.bool,
  pointer: Types.bool,
  focusVisible: Types.bool,
};

Alert.defaultProps = {
  type: 'info',
  small: false,
  dismissible: false,
  pointer: false,
  focusVisible: false,
};

export default Alert;



