/* eslint-disable no-console */
import React, { Component, Fragment } from 'react';

import FastFlagIcon from '@transferwise/icons/react/card-front'; // eslint-disable-line import/no-extraneous-dependencies

import { Checkbox, Select, RadioOption } from '..';
import MessageControl, { MessageType } from './MessageControl';
import image from '../common/captcha.png';

const MESSAGE_CONTENT_EXAMPLES = [
  {
    label: 'Raw text',
    content:
      'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Aspernatur aut facilis ipsam iusto nisi quae quis sunt voluptates. Cupiditate, veniam.',
  },
  {
    label: 'HTML',
    content: (
      <Fragment>
        Lorem ipsum dolor sit amet, <b>consectetur adipisicing elit</b>. Iure, labore!
      </Fragment>
    ),
  },
  {
    label: 'List',
    content: ['Make sure of this', 'and this', 'and avoid this, as well'],
  },
  {
    label: 'Image',
    content: image,
  },
];

const FORM_CONTROLS = [
  {
    label: 'Text Input',
    node: <input type="text" placeholder="Username" className="form-control" />,
    raw: '<input type="text" placeholder="Username" className="form-control" />',
  },
  {
    label: 'Checkbox',
    node: <Checkbox label="OK?" onChange={() => console.log('Check')} />,
    raw: '<Checkbox label="OK?" onChange={...} />',
  },
  {
    label: 'Radio Option',
    node: (
      <RadioOption
        id="test-radio-option"
        name="test-radio-option"
        description="Normally, the button and icon are vertically centered."
        onChange={() => console.log('Check')}
        media={<FastFlagIcon />}
        title="Radio option"
      />
    ),
    raw: '<RadioOption id=".." title="Radio option" onChange={...} />',
  },
];

export default class MessageControlDocs extends Component {
  static printMessageComponentProps(object) {
    return Object.keys(object)
      .map(key => `${key}="${object[key]}"\n`)
      .join('\t');
  }

  state = {
    focusVisible: false,

    childrenSelectedLabel: FORM_CONTROLS[0].label,
    children: FORM_CONTROLS[0].node,
    rawChildren: FORM_CONTROLS[0].raw,

    contentSelectedLabel: MESSAGE_CONTENT_EXAMPLES[0].label,
    content: MESSAGE_CONTENT_EXAMPLES[0].content,

    typeSelectedLabel: 'Info',
    type: MessageType.Info,
  };

  createStateLink(name) {
    return value => this.setState({ [name]: value });
  }

  render() {
    const {
      focusVisible,

      typeSelectedLabel,
      type,

      childrenSelectedLabel,
      rawChildren,
      children,

      contentSelectedLabel,
      content,
    } = this.state;

    const MESSAGE_TYPES = Object.keys(MessageType);

    return (
      <div className="container">
        <section className="section">
          <div className="row">
            <div className="col-md-6" id="message-control">
              <h2>MessageControl</h2>
              <p>Messaging for form controls</p>
            </div>
            <div className="col-md-6">
              <MessageControl {...{ content, type, focusVisible }}>{children}</MessageControl>
            </div>
          </div>
          <div className="row">
            <div className="col-md-6">
              <p>
                <code>MessageControl</code> with simple text input.
              </p>
              {/* eslint-disable react/jsx-indent */}
              <pre className="tw-docs-code">
                {`<MessageControl
    ${MessageControlDocs.printMessageComponentProps({ content, type, focusVisible })}>
    ${rawChildren}
</MessageControl>`}
              </pre>
            </div>

            <div className="col-md-6">
              <div className="row">
                <div className="col-md-6">
                  <label htmlFor="form-control-selector" className="control-label">
                    Children
                  </label>
                  <Select
                    id="form-control-selector"
                    selected={{ label: childrenSelectedLabel, value: children }}
                    required
                    options={FORM_CONTROLS.map(control => ({
                      label: control.label,
                      value: control.node,
                    }))}
                    onChange={selection =>
                      this.setState({
                        children: selection.value,
                        rawChildren: FORM_CONTROLS.filter(
                          control => control.label === selection.label,
                        )[0].raw,
                        childrenSelectedLabel: selection.label,
                      })
                    }
                  />
                </div>
                <div className="col-md-6">
                  <label htmlFor="message-content-selector" className="control-label">
                    Message Content Types
                  </label>
                  <Select
                    id="message-content-selector"
                    selected={{ label: contentSelectedLabel, value: content }}
                    required
                    options={MESSAGE_CONTENT_EXAMPLES.map(control => ({
                      label: control.label,
                      value: control.content,
                    }))}
                    onChange={selection =>
                      this.setState({
                        content: selection.value,
                        contentSelectedLabel: selection.label,
                      })
                    }
                  />
                </div>
                <div className="col-md-6">
                  <label htmlFor="button-block-checkbox" className="control-label">
                    Focus Visible (only for Info type)
                  </label>
                  <Checkbox
                    label="Focus Visible"
                    checked={focusVisible}
                    onChange={this.createStateLink('focusVisible')}
                  />
                </div>
                <div className="col-md-6">
                  <label htmlFor="message-content-selector" className="control-label">
                    Message Types
                  </label>
                  <Select
                    id="message-type-selector"
                    selected={{ label: typeSelectedLabel, value: type }}
                    required
                    options={MESSAGE_TYPES.map(key => ({
                      label: `${key}`,
                      value: MessageType[key],
                    }))}
                    onChange={selection =>
                      this.setState({ type: selection.value, typeSelectedLabel: selection.label })
                    }
                  />
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}



