import React, { isValidElement } from 'react';
import Types from 'prop-types';

import classNames from 'classnames';

import { isString, isArray } from '../common/validation/type-validators';

export const MessageType = {
  Info: 'info',
  Warning: 'warning',
  Error: 'error',
};

const MessageControl = ({ type, content, focusVisible, children }) => {
  function getMessageContent() {
    if (isString(content)) {
      if (isImagePath(content)) {
        const { title, label } = children.props;
        return <img className="thumbnail m-y-2" src={`${content}`} alt={title || label} />;
      }
      return content;
    }
    if (isArray(content) && content.length > 0) {
      return content.map((message, index) => <div key={index.toString()}>{message}</div>);
    }
    if (isValidElement(content)) {
      return content;
    }
    // eslint-disable-next-line
    console.error('Provided content is not supported', content);
    return null;
  }

  function isImagePath(string) {
    return string.match(/.jpg|.jpeg|.png/g);
  }

  const messageContent = getMessageContent();
  const groupClasses = classNames('form-group', {
    'has-info': type === MessageType.Info && !focusVisible,
    'has-error': type === MessageType.Error,
    'has-warning': type === MessageType.Warning,
  });
  const messageClasses = classNames(
    'alert',
    'alert-detach',
    {
      'alert-info': type === MessageType.Info,
      'alert-warning': type === MessageType.Warning,
      'alert-danger': type === MessageType.Error,
    },
    {
      'alert-focus': type === MessageType.Info && focusVisible,
    },
  );

  return (
    <div className={groupClasses}>
      {children}
      {messageContent && <div className={messageClasses}>{messageContent}</div>}
    </div>
  );
};

MessageControl.propTypes = {
  type: Types.oneOf(Object.values(MessageType)),
  children: Types.node,
  content: Types.oneOfType([Types.string, Types.node, Types.arrayOf(Types.string)]).isRequired,
  focusVisible: Types.bool,
};

MessageControl.defaultProps = {
  type: MessageType.Info,
  focusVisible: false,
  children: null,
};

export default MessageControl;



